<template>
       <el-table :data="tasks" @cell-click="linkToDataTable">
          <el-table-column prop="uuid" label="uuid" width="400">
          </el-table-column>
          <el-table-column prop="name" label="任务名称" sortable width="650">
          </el-table-column>
          <el-table-column prop="is_alive" label="是否进行中" width="50">
          </el-table-column>
          <el-table-column prop="result" label="输出">
          </el-table-column>
          <el-table-column prop="start_time" label="开始时间" :formatter="timeToDate">
          </el-table-column>
          <el-table-column prop="end_time" label="结束时间" :formatter="timeToDate">
          </el-table-column>
          <el-table-column prop="outputfile" label="输出文件" >
              
          </el-table-column>
          <el-table-column label="操作">
               <el-button type="danger" icon="el-icon-delete" circle @click="this.delete" prop="uuid"></el-button>
          </el-table-column>
   </el-table>
</template>

<script>

export default {
    name:"TaskList",
    data() {
        return {
            tasks:[]
            }
    },
    created(){
        this.$axios.get('/api/tasks').then((resp)=>{
            this.tasks = resp.data
            console.log(resp.data)
        }).catch((resp)=>{
            this.tasks = [
                {
                    "end_time": 1641571469.316159, 
                    "is_alive": false, 
                    "name": "Using shellSort to find the Max!INPUTFILE:100books.json OUTPUTFILE:test3.json X:100", 
                    "result": "\u5f85\u6392\u5e8f\u6570\u7ec4\u957f:100\n\u4e3b\u5faa\u73af\u6267\u884c\u4e86909\u6b21\n\u603b\u65f6\u95f4\uff1a0.00004100s", 
                    "start_time": 1641571469.3019688, 
                    "uuid": "7e8432c6-6fd3-11ec-bc05-4ee35ea174e5",
                    "outputfile":"100books.json"
                }
                ]
        })
    },
    methods:{
        delete:function(event){
            this.$axios.get(`/api/deleteTask?uuid=`+event.path[4].children[0].children[0].outerText).then(function(){
                window.location.reload()
            }).catch(function(){
                window.location.reload()
                // history.go(0)
            })
            // console.log(event.path[4])
        },
        linkToDataTable:function(r,a,b,c,d){
            // console.log(a)
            // console.log(a.label)
            // c.click = function(){window.location.href='/#/'}
            if(a.label!='操作')
            window.location.href=`/#/dataTable?file=${r.outputfile}`

        },
        timeToDate:function(r,c,v,l){
            // return new Date(v*1000).toString()
            return v
            }
    }
}
</script>



